*\r\n   * Prevent `sub` and `sup` elements from affecting the line height in\r\n   * all browsers.\r\n   */\r\n  \r\n  sub,\r\n  sup {\r\n    font-size: 75%;\r\n    line-height: 0;\r\n    position: relative;\r\n    vertical-align: baseline;\r\n  }\r\n  \r\n  sub {\r\n    bottom: -0.25em;\r\n  }\r\n  \r\n  sup {\r\n    top: -0.5em;\r\n  }\r\n  \r\n  /* Embedded content\r\n     ========================================================================== */\r\n  \r\n  /**\r\n   * Remove the border on images inside links in IE 10.\r\n   */\r\n  \r\n  img {\r\n    border-style: none;\r\n  }\r\n  \r\n  /* Forms\r\n     ========================================================================== */\r\n  \r\n  /**\r\n   * 1. Change the font styles in all browsers.\r\n   * 2. Remove the margin in Firefox and Safari.\r\n   */\r\n  \r\n  button,\r\n  input,\r\n  optgroup,\r\n  select,\r\n  textarea {\r\n    font-family: inherit; /* 1 */\r\n    font-size: 100%; /* 1 */\r\n    line-height: 1.15; /* 1 */\r\n    margin: 0; /* 2 */\r\n  }\r\n  \r\n  /**\r\n   * Show the overflow in IE.\r\n   * 1. Show the overflow in Edge.\r\n   */\r\n  \r\n  button,\r\n  input { /* 1 */\r\n    overflow: visible;\r\n  }\r\n  \r\n  /**\r\n   * Remove the inheritance of text transform in Edge, Firefox, and IE.\r\n   * 1. Remove the inheritance of text transform in Firefox.\r\n   */\r\n  \r\n  button,\r\n  select { /* 1 */\r\n    text-transform: none;\r\n  }\r\n  \r\n  /**\r\n   * Correct the inability to style clickable types in iOS and Safari.\r\n   */\r\n  \r\n  button,\r\n  [type=\"button\"],\r\n  [type=\"reset\"],\r\n  [type=\"submit\"] {\r\n    -webkit-appearance: button;\r\n  }\r\n  \r\n  /**\r\n   * Remove the inner border and padding in Firefox.\r\n   */\r\n  \r\n  button::-moz-focus-inner,\r\n  [type=\"button\"]::-moz-focus-inner,\r\n  [type=\"reset\"]::-moz-focus-inner,\r\n  [type=\"submit\"]::-moz-focus-inner {\r\n    border-style: none;\r\n    padding: 0;\r\n  }\r\n  \r\n  /**\r\n   * Restore the focus styles unset by the previous rule.\r\n   */\r\n  \r\n  button:-moz-focusring,\r\n  [type=\"button\"]:-moz-focusring,\r\n  [type=\"reset\"]:-moz-focusring,\r\n  [type=\"submit\"]:-moz-focusring {\r\n    outline: 1px dotted ButtonText;\r\n  }\r\n  \r\n  /**\r\n   * Correct the padding in Firefox.\r\n   */\r\n  \r\n  fieldset {\r\n    padding: 0.35em 0.75em 0.625em;\r\n  }\r\n  \r\n  /**\r\n   * 1. Correct the text wrapping in Edge and IE.\r\n   * 2. Correct the color inheritance from `fieldset` elements in IE.\r\n   * 3. Remove the padding so developers are not caught out when they zero out\r\n   *    `fieldset` elements in all browsers.\r\n   */\r\n  \r\n  legend {\r\n    box-sizing: border-box; /* 1 */\r\n    color: inherit; /* 2 */\r\n    display: table; /* 1 */\r\n    max-width: 100%; /* 1 */\r\n    padding: 0; /* 3 */\r\n    white-space: normal; /* 1 */\r\n  }\r\n  \r\n  /**\r\n   * Add the correct vertical alignment in Chrome, Firefox, and Opera.\r\n   */\r\n  \r\n  progress {\r\n    vertical-align: baseline;\r\n  }\r\n  \r\n  /**\r\n   * Remove the default vertical scrollbar in IE 10+.\r\n   */\r\n  \r\n  textarea {\r\n    overflow: auto;\r\n  }\r\n  \r\n  /**\r\n   * 1. Add the correct box sizing in IE 10.\r\n   * 2. Remove the padding in IE 10.\r\n   */\r\n  \r\n  [type=\"checkbox\"],\r\n  [type=\"radio\"] {\r\n    box-sizing: border-box; /* 1 */\r\n    padding: 0; /* 2 */\r\n  }\r\n  \r\n  /**\r\n   * Correct the cursor style of increment and decrement buttons in Chrome.\r\n   */\r\n  \r\n  [type=\"number\"]::-webkit-inner-spin-button,\r\n  [type=\"number\"]::-webkit-outer-spin-button {\r\n    height: auto;\r\n  }\r\n  \r\n  /**\r\n   * 1. Correct the odd appearance in Chrome and Safari.\r\n   * 2. Correct the outline style in Safari.\r\n   */\r\n  \r\n  [type=\"search\"] {\r\n    -webkit-appearance: textfield; /* 1 */\r\n    outline-offset: -2px; /* 2 */\r\n  }\r\n  \r\n  /**\r\n   * Remove the inner padding in Chrome and Safari on macOS.\r\n   */\r\n  \r\n  [type=\"search\"]::-webkit-search-decoration {\r\n    -webkit-appearance: none;\r\n  }\r\n  \r\n  /**\r\n   * 1. Correct the inability to style clickable types in iOS and Safari.\r\n   * 2. Change font properties to `inherit` in Safari.\r\n   */\r\n  \r\n  ::-webkit-file-upload-button {\r\n    -webkit-appearance: button; /* 1 */\r\n    font: inherit; /* 2 */\r\n  }\r\n  \r\n  /* Interactive\r\n     ========================================================================== */\r\n  \r\n  /*\r\n   * Add the correct display in Edge, IE 10+, and Firefox.\r\n   */\r\n  \r\n  details {\r\n    display: block;\r\n  }\r\n  \r\n  /*\r\n   * Add the correct display in all browsers.\r\n   */\r\n  \r\n  summary {\r\n    display: list-item;\r\n  }\r\n  \r\n  /* Misc\r\n     ========================================================================== */\r\n  \r\n  /**\r\n   * Add the correct display in IE 10+.\r\n   */\r\n  \r\n  template {\r\n    display: none;\r\n  }\r\n  \r\n  /**\r\n   * Add the correct display in IE 10.\r\n   */\r\n  \r\n  [hidden] {\r\n    display: none;\r\n  }","/*Buttons*/\r\n\r\n.success-button {\r\n  padding: 2.5px 20px;\r\n  @include text;\r\n  background-color: $success;\r\n  border: 1px solid $success;\r\n  border-radius: 5px;\r\n  color: $white;\r\n\r\n  &:hover {\r\n    background-color: #80f0a6;\r\n    border: 1px solid #80f0a6;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n  &:active {\r\n    background-color: #4BD379;\r\n    border: 1px solid #4BD379;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.default-button {\r\n  @include text;\r\n  background-color: $disabled-text;\r\n  border: 1px solid $text-color;\r\n  border-radius: 5px;\r\n  color: $black;\r\n  padding: 2.5px 20px;\r\n\r\n  &:hover {\r\n    background-color: #e1e1e1;\r\n    border: 1px solid $text-color;\r\n    border-radius: 5px;\r\n    color: $black;\r\n  }\r\n\r\n  &:active {\r\n    background-color: #cfcfcf;\r\n    border: 1px solid $text-color;\r\n    border-radius: 5px;\r\n    color: $black;\r\n  }\r\n}\r\n\r\n.primary-button {\r\n  @include text;\r\n  background-color: $primary;\r\n  border: 1px solid $primary;\r\n  border-radius: 5px;\r\n  color: $white;\r\n  padding: 2.5px 20px;\r\n\r\n  &:hover {\r\n    background-color: #8a73ff;\r\n    border: 1px solid #8a73ff;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n  &:active {\r\n    background-color: #715be3;\r\n    border: 1px solid #715be3;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.error-button {\r\n  @include text;\r\n  background-color: $error;\r\n  border: 1px solid $error;\r\n  border-radius: 5px;\r\n  color: $white;\r\n  padding: 2.5px 20px;\r\n\r\n  &:hover {\r\n    background-color: #ff8080;\r\n    border: 1px solid #ff8080;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n  &:active {\r\n    background-color: #d65d5d;\r\n    border: 1px solid #d65d5d;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n.disabled-button {\r\n  @include text;\r\n  background-color: $inner-shadow;\r\n  border: 1px solid $inner-shadow;\r\n  border-radius: 5px;\r\n  color: $label-text;\r\n  padding: 2.5px 20px;\r\n}\r\n\r\n/*Status*/\r\n\r\n.status-complete {\r\n  background-color: #4BD379;\r\n  border: 1px solid #157536;\r\n  border-radius: 5px;\r\n  color: #157536;\r\n  @include text-button;\r\n}\r\n\r\n.status-inProgress {\r\n  background-color: #FFC700;\r\n  border: 1px solid #846F00;\r\n  border-radius: 5px;\r\n  color: #846F00;\r\n  @include text-button;\r\n}\r\n\r\n.status-testing {\r\n  background-color: #FFC700;\r\n  border: 1px solid #846F00;\r\n  border-radius: 5px;\r\n  color: #846F00;\r\n  @include text-button;\r\n}\r\n\r\n.status-opened {\r\n  background-color: $label-text;\r\n  border: 1px solid $text-color;\r\n  border-radius: 5px;\r\n  color: $text-color;\r\n  @include text-button;\r\n}\r\n\r\n.status-danger {\r\n  background-color: #EB4F4F;\r\n  border: 1px solid #6D1313;\r\n  border-radius: 5px;\r\n  color: #6D1313;\r\n  @include text-button;\r\n}\r\n\r\n/*Pagination*/\r\n\r\n.pagination__back,\r\n.pagination__forward {\r\n  padding: 2.5px 10px;\r\n  margin-right: 5px;\r\n  max-height: 24px;\r\n  min-width: 67px;\r\n  @include text;\r\n\r\n  border-radius: 5px;\r\n\r\n  &--disabled {\r\n    background-color: $inner-shadow;\r\n    border: 1px solid $inner-shadow;\r\n    border-radius: 5px;\r\n    color: $label-text;\r\n    cursor: not-allowed;\r\n  }\r\n\r\n  &--default {\r\n    background-color: $disabled-text;\r\n    border: 1px solid $text-color;\r\n    border-radius: 5px;\r\n    color: $black;\r\n\r\n    &:hover {\r\n      background-color: #e1e1e1;\r\n      border: 1px solid $text-color;\r\n      border-radius: 5px;\r\n      color: $black;\r\n    }\r\n\r\n    &:active {\r\n      background-color: #cfcfcf;\r\n      border: 1px solid $text-color;\r\n      border-radius: 5px;\r\n      color: $black;\r\n    }\r\n  }\r\n}\r\n\r\n.pagination__number {\r\n  padding: 2.5px 7.5px;\r\n  margin-right: 5px;\r\n  max-width: 24px;\r\n  max-height: 24px;\r\n  @include text;\r\n\r\n  border-radius: 5px;\r\n\r\n  &--default {\r\n    background-color: $disabled-text;\r\n    border: 1px solid $text-color;\r\n    border-radius: 5px;\r\n    color: $black;\r\n  }\r\n\r\n  &--stay {\r\n    background-color: $primary;\r\n    border: 1px solid $primary;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n  &:hover {\r\n    background-color: #8a73ff;\r\n    border: 1px solid #8a73ff;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n  &:active {\r\n    background-color: #715be3;\r\n    border: 1px solid #715be3;\r\n    border-radius: 5px;\r\n    color: $white;\r\n  }\r\n\r\n}\r\n\r\n/*Type*/\r\n\r\n.square {\r\n  width: 24px;\r\n  height: 24px;\r\n\r\n  position: relative;\r\n  z-index: 1;\r\n\r\n  border-radius: 5px;\r\n\r\n  &-task {\r\n    background: #00D1FF;\r\n  }\r\n\r\n  &-bug {\r\n    background: #EB4F4F;\r\n  }\