border: 1px solid $inner-shadow;\r\n  border-radius: 5px;\r\n  background: no-repeat center url(../img/x.svg);\r\n  background-size: 10px;\r\n\r\n}\r\n\r\n.checkbox-disabled-not-empty {\r\n  background-color: $inner-shadow;\r\n  border: 1px solid $inner-shadow;\r\n  border-radius: 5px;\r\n}\r\n\r\n/*Input*/\r\n\r\n.input__empty {\r\n  background-color: $white;\r\n  box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n  border-radius: 3px;\r\n}\r\n\r\n.input__default {\r\n  background-color: $white;\r\n  border: 1px solid $primary;\r\n  box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n  border-radius: 3px;\r\n\r\n  &:hover {\r\n    background-color: $white;\r\n    border: 1px solid $primary;\r\n    box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n    border-radius: 3px;\r\n\r\n  }\r\n\r\n  &:focus {\r\n    background-color: $white;\r\n    border: 1px solid $primary;\r\n    box-shadow: 0px 0px 2px 2px rgba(123, 97, 255, 0.5);\r\n    border-radius: 3px;\r\n  }\r\n}\r\n\r\n.input__disabled {\r\n  background: $inner-shadow;\r\n  box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n  border-radius: 3px;\r\n}\r\n\r\n.input__success {\r\n  background-color: $white;\r\n  border: 1px solid $success;\r\n  box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n  border-radius: 3px;\r\n\r\n  &:hover {\r\n    background-color: $white;\r\n    border: 1px solid $success;\r\n    box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n    border-radius: 3px;\r\n\r\n  }\r\n\r\n  &:focus {\r\n    background-color: $white;\r\n    border: 1px solid $success;\r\n    box-shadow: 0px 0px 2px 2px rgba(97, 255, 151, 0.5);\r\n    border-radius: 3px;\r\n  }\r\n}\r\n\r\n.input__error {\r\n  background-color: $white;\r\n  border: 1px solid $error;\r\n  box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n  border-radius: 3px;\r\n\r\n  &:hover {\r\n    background-color: $white;\r\n    border: 1px solid $error;\r\n    box-shadow: inset 0px 0px 2px 1px $inner-shadow;\r\n    border-radius: 3px;\r\n\r\n  